<template>
  <div class="container">
    <b-modal
      class="modal-backdrop"
      id="progressionModal"
      :title="activity.nom"
      hide-backdrop
    >
      <div class="modal-body">
        <p>{{ getCommentFromState(progression.state) }}</p>
        <p v-if="progression.commentaire && progression.commentaire.length">
