        case "REVIEWING":
          return "Ton activit√© est en cours de validation par un chef ou une cheftaine";
        case "VALIDATED":
          return "Ton activit√© a √©t√© valid√©e ! Bravo √† toi !";
        case "REFUSED":
          return "Ton activit√© n'a pas √©t√© valid√©e mais tu as la possibilit√© de la refaire !";
        default:
          return "Cette activit√© est dans un √©tat √©trange... Recharge la page pour voir ?";
      }
    }
  }
};
</script>

